const chineseTranslation = (romanization, example) => {
    return `Make sure to translate words and not individual characters, e.g.  should be one word and not two like  and . Include the ${romanization} and the direct meaning of the separate characters too. Example: ${example}`;
}

export const NO_TRANSLATION_CONTEXT = null;
export const LANGUAGES = [
    { code: 'french', emoji: '', name: 'French', translation_context: NO_TRANSLATION_CONTEXT },
    { code: 'spanish', emoji: '', name: 'Spanish', translation_context: NO_TRANSLATION_CONTEXT },
    { code: 'mandarin', emoji: '', name: 'Mandarin', translation_context: chineseTranslation('pinyin', 'e.g.  is translated as "hello -  ni3 (you)  hao3 (good)"') },
    { code: 'cantonese', emoji: '', name: 'Cantonese', translation_context: chineseTranslation('jyutping', 'e.g.  is translated as "hello -  nei5 (you)  hou2 (good)"') },
];
